ackground: #2196F3;\r\n  color: white;\r\n  font-size: 17px;\r\n  border: 1px solid grey;\r\n  border-left: none;\r\n  cursor: pointer;\r\n}\r\n\r\nform.cari button:hover {\r\n  background: #0b7dda;\r\n}\r\n\r\nform.cari::after {\r\n  content: \"\";\r\n  clear: both;\r\n  display: table;\r\n}\r\n\r\nbutton:disabled,\r\nbutton[disabled]{\r\n  border: 1px solid #999999;\r\n  background-color: #cccccc;\r\n  color: #666666;\r\n}\r\n\r\n/*\r\npicture.img {\r\n  background-image: url(\"./images/hero-image_3.jpg\");\r\n}\r\n@media (min-width: 1000px) {\r\n  picture.img {\r\n    background-image: url(\"./images/hero-image_1.jpg\");\r\n  }\r\n}\r\n*/\r\n\r\n.hero {\r\n  display: flex;\r\n  align-items: center;\r\n  min-height: 380px;\r\n  width: 100%;\r\n  text-align: center;\r\n  background-position: center;\r\n  background-image: url(\"./images/hero-image_1.jpg\");\r\n  background-color: green;\r\n  }\r\n\r\n\r\n@media (min-width: 1000px) {\r\n  .hero {\r\n    background-image: url(\"./images/hero-image_3.jpg\");\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
